@import url('https://fonts.googleapis.com/css2?family=Playwrite+AU+TAS:wght@100..400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #ffffff;
}

.grey {
  background-color: #0e0e0e;
}

html {
  scroll-behavior: smooth;
  background-color: #000000;
  font-family: "Playwrite AU TAS", cursive;
}

a {
  color: #ffffff;
}

section {
  background-color: #000000;
}

.container {
  max-width: 1920px;
  padding: 0 15px;
}

.citata {
  width: 100%;
  font-size: 4rem;
  text-align: center;
  margin: auto;
  margin-top: 70px;
  margin-bottom: 80px;
  padding: 25px 50px;
}

.citata h1 {
  width: 55vw;
  margin: 0 auto;
  font-weight: 500;
  line-height: 7rem;
}

.hands {
  height: 120vh;
  width: 100%;
  background-image: url("../images/wedding_hands.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  filter: grayscale(100%);
  background-position-y: 152px;

}

@media(max-width: 430px) {
  .container {
    max-width: 430px;
  }

  .citata {
    width: 80vw;
    font-size: 1.5rem;
    margin-top: 40px;
    margin-bottom: 40px;
    padding: 25px 50px;
  }

  .citata h1 {
    font-weight: 500;
    line-height: 3rem;
    text-align: center;
  }

  .hands {
    height: 60vh;
    filter: grayscale(100%);
    background-position-y: 350px;
    background-position-x: -600px;
  }
}