a {
  text-decoration: none;
  color: #000;
}

a img {
  width: 100px;
  height: 100px;
}

.footer {
  padding: 25px 100px;
  margin-top: 125px;
  background-color: #0e0e0e;
}

.socials {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.vk_social {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 15px;
}

.hearts {
  height: 120px;
  width: 260px;
  background-image: url("../../images/310427.svg");
  background-position: center center;
  background-size: cover;
  background-repeat: repeat-x;
}

/* Поддержа авторов */

.help {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.help h3 {
  text-align: center;
  margin-bottom: 20px;
}

.authors {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 50px;
}

.authors a {
  color: #000000;
}

.authors img {
  width: 50px;
  height: 50px;
}

.btn__wrapper {
  display: flex;
  justify-content: center;
  margin: 100px 0 30px 0;
}

.btn {
  /*display: block;*/
  background-color: transparent;
  border: 1px solid #ffdf3f;
  border-radius: 50px;
  padding: 25px 50px;
  font-size: 1rem;
  width: fit-content;
}

@media(max-width: 430px) {
  .footer {
    padding: 0 50px;
    margin-top: 65px;
  }

  .socials {
    display: flex;
    align-items: start;
    justify-content: space-between;
    padding-top: 25px;
  }

  .vk_social a {
    width: 62px;
    height: 50px;
    text-align: center;
  }

  .vk_social img {
    width: 50px;
    height: 50px;
  }

  .hearts {
    height: 60px;
    width: 130px;
  }

  .btn__wrapper {
    display: flex;
    justify-content: center;
    margin: 50px 0 30px 0;
  }

  .btn {
    padding: 20px 40px;
  }
}