.hope {
  margin-top: 62px;
  padding-top: 80px;
  position: relative;
}

.hope h1 {
  text-align: center;
  font-size: 7rem;
  font-weight: 500;
  line-height: 7rem;
  color: #ffffff;
}

.hope button {
  margin: 10vh auto;
  text-align: center;
}

.btn {
  background-color: transparent;
  border: 1px solid #ffdf3f;
  border-radius: 50px;
  padding: 25px 50px;
  font-size: 1rem;
  width: fit-content;
}

.btn__wrapper {
  display: flex;
  justify-content: center;
}

.check {
  width: 100px;
  text-align: center;
}

@media(max-width: 430px) {
  .hope h1 {
    font-size: 4rem;
    font-weight: 500;
    line-height: 4.3rem;
  }

  .btn__wrapper {
    display: flex;
    justify-content: center;
  }

  .btn {
    padding: 20px 40px;
  }

  .hope button {
    margin: 40px auto;
  }
}