.timer {
  background-blend-mode: multiply;
  background-image: url("../../images/timer_bg1.png");
  background-repeat: repeat-y;
  background-position: center center;
  background-size: cover;
  background-color: rgba(0, 0, 0, 0.81);

  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 4em;
  padding-top: 60px;
  padding-bottom: 100px;
  width: 100%;
  height: 100%;
}

.timer div:first-child {
  text-align: center;
}

h2 {
  font-weight: 600;
  margin-bottom: 0.5em;
  font-size: 3em;
}

.timer div:first-child p {
  font-size: 2em;
  color: #fff;
}

.timer div:first-child h2 {
  color: #fff;
}

.timer div:last-child {
  border: 1px solid #ffdf3f;
  border-radius: 30px;
  display: flex;
  justify-content: space-between;
  margin-top: 3em;
  padding: 3em;
  text-align: center;
  width: 500px;

}

section p:first-child,
.timer div:last-child span {
  font-size: 3em;
}

.timer div:last-child section {
  background-color: transparent;
}

.timer div:last-child section p {
  color: #fff;
}

.timer div:last-child section small {
  color: #fff;
}

@media(max-width: 430px) {
  .timer {
    width: 430px;
  }

  .timer div:last-child section p {
    font-size: 2rem;
  }

  .timer div:last-child section p:last-child {
    font-size: 1rem;
  }

  .timer div:first-child p {
    font-size: 1em;
  }

  .timer div:first-child h2 {
    font-size: 2em;
  }

  .timer div:last-child {
    margin-top: 2em;
    padding: 2em;
    width: 430px;

  }
}