.home {
  display: flex;
  flex-direction: column;
}

.title {
  font-size: 7rem;
}

.info {
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
  align-items: start;
  font-size: 1.3rem;
}

.adress {
  font-size: 1.9rem;
}

.wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 40px;
}

@media(max-width: 430px) {
  .title__wrapper {
    display: flex;
    justify-content: center;
  }

  .title {
    font-size: 3rem;
  }

  .info h3 {
    font-size: 1rem;
  }

  .info div img {
    width: 100px;
    height: 200px;
  }
}