.modal-invite {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    z-index: 100;
}

.modal-invite.active {
    opacity: 1;
    pointer-events: all;
}

.modal-invite__content {
    padding: 20px;
    border-radius: 20px;
    background-color: #fff;
    transform: scale(0.5);
    transition: all 0.4s;
    width: 50vw;
}

.modal-invite__content h1 {
    color: #000;
}

@media(max-width: 430px) {
    .modal-invite__content {
        width: 500px;
        height: 500px;
    }

    .modal-invite {
        height: 100vh;
        top: 0;
        left: 0;
    }
}