.album {
    background-color: #0e0e0e;

}

.photos {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 150px 70px;
}

@media(max-width: 430px) {
    .album {
        margin-bottom: 60px;
    }

    .photos {
        padding: 50px 35px;
    }

    .photos img {
        width: 100px;
        height: 200px;
    }
}