.main_history {
  padding-top: 150px;
  padding-bottom: 100px;
  background-color: #0e0e0e;
}

.phrase {
  font-size: 40px;
  margin-bottom: 55px;
}

.mainList {
  margin-left: 30px;
}

.list {
  display: flex;
  align-items: start;
  column-gap: 15px;
  margin-bottom: 25px;
}

.list img {
  width: 50px;
}

.listInfo {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
}

.history {
  display: flex;
}

.listName {
  font-size: 1.7rem;
  font-weight: 500;
}

.text {
  font-size: 1.3rem;
  line-height: 1.3rem;
  padding-left: 15px;
}

@media (max-width: 430px) {
  .main_history {
    margin-top: 50px;
  }

  .phrase {
    font-size: 20px;
    margin-bottom: 35px;
  }

  .history img {
    width: 300px;
    height: 100px;
  }

  .list {
    column-gap: 10px;
    margin-bottom: 15px;
  }

  .list img {
    width: 25px;
  }

  .listName {
    font-size: 1rem;
    font-weight: 500;
  }

  .text {
    font-size: 0.8rem;
    line-height: 1rem;
    padding-left: 10px;
  }
}