.main_promises {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #0e0e0e;
}

.promises {
  display: flex;
  align-items: start;
  justify-content: space-between;
}

.promises_text {
  width: 40vw;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.promises_text h3 {
  font-size: 2.7rem;
  font-weight: 500;
}

.promises_text p {
  text-align: justify;
  font-size: 2rem;
  line-height: 3rem;
}

@media(max-width: 430px) {
  .promises_text h3 {
    font-size: 1.4rem;
    font-weight: 500;
  }

  .promises_text p {
    text-align: start;
    font-size: 1rem;
    line-height: 1rem;
  }

  .promises div:last-child img {
    width: 200px;
    height: 400px;
  }

  .main_promises {
    margin-top: 60px;
    padding: 20px 0;
  }
}