.invite {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  margin-top: 150px;
  padding-bottom: 50px;
}

.letter {
  margin-top: 50px;
  text-align: center;
}

.letter h2 {
  font-size: 4rem;
}

.letter h3 {
  font-size: 2.8em;
}

.letter p {
  margin-top: 20px;
  font-size: 40px;
}

@media(max-width: 430px) {
  .invite {
    margin-top: 50px;
  }

  .letter h2 {
    font-size: 2.5rem;
  }

  .letter h3 {
    font-size: 2rem;
  }

  .letter p {
    margin-top: 10px;
    font-size: 20px;
  }
}