.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal__content {
  padding: 20px;
  border-radius: 20px;
  background-color: #fff;
  transform: scale(0.5);
  transition: all 0.4s;
  width: 50vw;
}

.modal__content.active {
  transform: scale(1);
}

@media(max-width: 430px) {
  .modal {
    top: 0;
  }
}